.recs-header {
  margin-bottom: 2rem;
}
.recs-header .header-content {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 1.5rem;
}
.recs-header .header-content .header-main {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  flex: 1;
}
.recs-header .header-content .header-main .header-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  border-radius: 1rem;
  color: white;
  flex-shrink: 0;
  box-shadow: 0 4px 6px -1px rgba(139, 92, 246, 0.1), 0 2px 4px -1px rgba(139, 92, 246, 0.06);
}
.recs-header .header-content .header-main .header-icon svg {
  width: 2rem;
  height: 2rem;
}
.recs-header .header-content .header-main .header-text {
  flex: 1;
}
.recs-header .header-content .header-main .header-text .header-title {
  font-size: 2.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.75rem 0;
  line-height: 1.2;
}
.recs-header .header-content .header-main .header-text .header-description {
  font-size: 1.125rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.6;
  max-width: 42rem;
}
.recs-header .header-content .last-updated {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 0.75rem;
  color: #6b7280;
  font-size: 0.875rem;
  font-weight: 500;
  white-space: nowrap;
}
.recs-header .header-content .last-updated .update-icon {
  width: 1rem;
  height: 1rem;
  color: #8b5cf6;
}
.recs-header .action-bar .action-buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}
.recs-header .action-bar .action-buttons .btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 1px solid transparent;
  text-decoration: none;
}
.recs-header .action-bar .action-buttons .btn .btn-icon {
  width: 1rem;
  height: 1rem;
  flex-shrink: 0;
}
.recs-header .action-bar .action-buttons .btn .btn-text {
  white-space: nowrap;
}
.recs-header .action-bar .action-buttons .btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
}
.recs-header .action-bar .action-buttons .btn.btn-primary {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  color: white;
  border-color: #8b5cf6;
}
.recs-header .action-bar .action-buttons .btn.btn-primary:hover:not(:disabled) {
  background: linear-gradient(135deg, #7c3aed 0%, #6d28d9 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 6px -1px rgba(139, 92, 246, 0.3), 0 2px 4px -1px rgba(139, 92, 246, 0.2);
}
.recs-header .action-bar .action-buttons .btn.btn-primary:active:not(:disabled) {
  transform: translateY(0);
}
.recs-header .action-bar .action-buttons .btn.btn-secondary {
  background: white;
  color: #374151;
  border-color: #d1d5db;
}
.recs-header .action-bar .action-buttons .btn.btn-secondary:hover:not(:disabled) {
  background: #f9fafb;
  border-color: #9ca3af;
  transform: translateY(-1px);
}
.recs-header .action-bar .action-buttons .btn.btn-secondary:active:not(:disabled) {
  transform: translateY(0);
}
.recs-header .action-bar .action-buttons .btn.btn-outline {
  background: transparent;
  color: #8b5cf6;
  border-color: #8b5cf6;
}
.recs-header .action-bar .action-buttons .btn.btn-outline:hover:not(:disabled) {
  background: #f3f4f6;
  transform: translateY(-1px);
}
.recs-header .action-bar .action-buttons .btn.btn-outline:active:not(:disabled) {
  transform: translateY(0);
}

@media (max-width: 768px) {
  .recs-header .header-content {
    flex-direction: column;
    align-items: stretch;
    gap: 1.5rem;
  }
  .recs-header .header-content .header-main {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1rem;
  }
  .recs-header .header-content .header-main .header-icon {
    width: 3rem;
    height: 3rem;
  }
  .recs-header .header-content .header-main .header-icon svg {
    width: 1.5rem;
    height: 1.5rem;
  }
  .recs-header .header-content .header-main .header-text .header-title {
    font-size: 1.875rem;
  }
  .recs-header .header-content .header-main .header-text .header-description {
    font-size: 1rem;
  }
  .recs-header .header-content .last-updated {
    align-self: center;
  }
  .recs-header .action-bar .action-buttons {
    justify-content: center;
    gap: 0.75rem;
  }
  .recs-header .action-bar .action-buttons .btn {
    padding: 0.625rem 1.25rem;
    font-size: 0.8rem;
  }
}
